<template>
  <div class="index">
    <div
      class="index_body"
      v-if="zhezhao || loading"
      v-loading="loading"
      element-loading-text="拼命加载中"
      element-loading-spinner="el-icon-loading"
      element-loading-background="rgba(0, 0, 0, 0.6)"
    ></div>
    <el-container>
      <!-- 头部 -->
      <el-header>
        <div class="index_title">守护者平台</div>
      </el-header>
      <!-- 主体 -->
      <el-main>
        <!-- 标题 -->
        <el-row :gutter="24">
          <el-col :xs="5" :sm="3">标题:</el-col>
          <el-col :xs="19" :sm="21">
            <el-input
              type="text"
              placeholder="请输入内容(可为空)"
              v-model="title"
              maxlength="30"
              show-word-limit
            >
            </el-input>
          </el-col>
        </el-row>
        <div style="margin: 30px 0"></div>
        <!-- 类型 -->
        <el-row :gutter="24">
          <el-col :xs="5" :sm="3">类型:</el-col>
          <el-col :xs="19" :sm="21">
            <el-select v-model="value" placeholder="请选择">
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </el-col>
        </el-row>
        <div style="margin: 30px 0"></div>
        <!-- 内容 -->
        <el-input
          type="textarea"
          :autosize="{ minRows: 2 }"
          placeholder="请输入内容（可为空）"
          v-model="textarea"
          maxlength="500"
          show-word-limit
        >
        </el-input>
        <el-alert
          title="涉及第三方,禁止传输私密内容"
          type="warning"
          center
          :closable="false"
          show-icon
        >
        </el-alert>
        <div style="margin: 30px 0"></div>
        <!-- 发送 -->
        <div class="btn_send_f">
          <el-button class="btn_send" type="primary" :disabled="isDisabled" @click="zhezhao = true">{{sendtext}}</el-button>
        </div>
      </el-main>
      <el-footer class="index_db">
        <div>该信息会发送到管理员微信</div>
          <div>仅供学习测试</div>
      </el-footer>
      <div style="color:red">{{error2}}</div>
    </el-container>
    <transition name="el-zoom-in-center">
      <el-card class="box-card" v-if="zhezhao">
        <h2>{{ title }}【{{ options[value - 1].label }}】</h2>
        <p>{{ textarea }}</p>
        <el-button @click="zhezhao = false">取消</el-button>
        <el-button type="primary" @click="handleClose">发送</el-button>
      </el-card>
    </transition>
  </div>
</template>

<script>
export default {
  data() {
    return {
      dialogVisible: false,
      isDisabled:false,
      sendtext:'确定',
      time:30,
      error2:'',
      // 类型
      options: [
        {
          value: "1",
          label: "日常",
        },
        {
          value: "2",
          label: "紧急",
        },
      ],
      // 标题
      title: "",
      // 类型
      value: "1",
      // 主要内容
      textarea: "",
      //遮罩
      zhezhao: false,
      // loading
      loading: false,
      ruleForm:{
         text: "YRCY",
        desp:"FFFFF"
      },
      // 域名
      url:'/SCU85599Tb35f448051961889abeb49721be788045e5102e9551ce.send',
    };
  },
  mounted() {},
  methods: {
    fanhui(){
         sessionStorage.setItem("ss.rs7.top_user_token", "");
        this.$router.push({ path: "/login" });
    },
    // 发送确认
    handleClose() {
      let _this=this;
      this.zhezhao = false;
      this.loading = true;
       
      this.$axios({
        url:'/SCU85599Tb35f448051961889abeb49721be788045e5102e9551ce.send',
        method: "get",
        params: {
          text: this.title + "【" + this.options[this.value - 1].label + "】",
          desp: this.textarea,
        },
        headers:{
          'Content-Type':'application/x-www-form-urlencoded',
        },
      }).then(
        () => {
          // console.log(res;
          _this.loading=false;
          _this.$notify({
          title: '成功',
          message: '发送成功。',
          type: 'success',
            offset: 20
        });
        },
        function (error) {
          _this.error2=error;
          _this.loading=false;
          _this.sendMsg();
           _this.$notify.error({
          title: '失败',
          message: '发送失败,请联系管理员',
            offset: 20
        });
        }
      );
      
    },
    // 倒计时
    sendMsg() {
					let me = this;
					me.isDisabled = true;
					let interval = window.setInterval(function() {
						me.sendtext = '（' + me.time + '秒）后可确认';
						--me.time;
						if(me.time < 0) {
							me.sendtext = "确认";
							me.time = 10;
							me.isDisabled = false;
							window.clearInterval(interval);
						}
					}, 1000);
  },
  }
}
</script>


<style lang="less" scoped>
// 遮罩
.index_body {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.6);
  z-index: 10;
}
// 弹出
.box-card {
  position: fixed;
  top: 10%;
  left: 5%;
  right: 5%;
  bottom: 10%;
  z-index: 11;
}
.index_title {
  font: normal 800 35px "黑体";
}
.btn_send_f {
  width: 100%;
}
.btn_send {
  width: 100%;
  text-align: center;
}
.index_db div {
  font: normal 400 15px "黑体";
  text-align: center;
}
// 适配手机
@media screen and (min-width: 640px) {
  // 页面标题文字
  .index{
    width: 30%;
    margin: 100px auto;
   box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04)
  }
  .box-card{
    width: 50%;
    margin: 0 auto;
    height: 70%;
  }

}
</style>
